<template>
    <div class="myCheck">
        <div class="nav">
            <span>创建时间</span>
            <el-date-picker
                class="input_c"
                v-model="cTime"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期">
            </el-date-picker>
            <span>类型</span>
            <bda-select
                class="input_c"
                v-model="type"
                size="small"
                filterable
                clearable
                placeholder="请选择类型"
            >
                <bda-option
                    v-for="item in form.typeList"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                >
                </bda-option>
            </bda-select>
            <span>状态</span>
            <bda-select
                class="input_c"
                v-model="status"
                size="small"
                filterable
                clearable
                placeholder="请选择状态"
            >
                <bda-option
                    v-for="item in form.statusList"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                >
                </bda-option>
            </bda-select>
            <bda-button @click="getDatas">查询</bda-button>
        </div>
        <div style="height: calc(100% - 20px)">
            <element-bda-table-page
                v-loading="dsLoading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :[tableData]="majorArray"
                :columnArray="majorCol"
                :pageSize = pagesRs.pageSize
                :pageNo = pagesRs.pageNo
                :total = pagesRs.total
                @getRow="getMajorRow"
                @linkRow="linkRowClick"
                @getPageNo="changePageNo"
                @getPageSize="changePageSize">
            </element-bda-table-page>
        </div>
    </div>
</template>

<script>
  import elementBdaTablePage from '@/components/table/element-bda-table-page'
  import Interface from "../../../../../base/mixin/enterprise-manage-center";
  import {
    API_PATH
  } from '@/config'
  export default {
    name:'myCheck',
    mixins: [Interface],
    components:{
      elementBdaTablePage
    },
    props: [],
    data() {
      return {
        form:{
          typeList:[
            {name:'新增', value:'新增'},
            {name:'修改', value:'修改'},
            {name:'删除', value:'删除'},
            {name:'停用', value:'停用'},
            {name:'启用', value:'启用'},],
          statusList:[
            {name:'审核不通过', value:'0'},
            {name:'待审核', value:'1'},
            {name:'审核通过', value:'2'},
            {name:'已撤销', value:'-1'},
            {name:'已废除', value:'-2'},
          ]
        },
        tableData:'tableData',
        majorArray: [],
        majorCol:[],
        dsLoading:false,
        pagesRs:{
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        keyWords:'',
        cTime:'',
        status:'',
        type:'',
      };
    },
    mounted() {
      this.getDatas()
    },
    created() {
      this.getDepartment()
    },
    watch: {

    },
    methods: {
      //所属部门
      getDepartment(){
        this.department({}).then((res)=>{
          this.departmentArray = res||[]
        })
      },
      departmentChange(v){
        for (let i =0;i<this.departmentArray.length;i++){
          if (v===this.departmentArray[i].code){
            this.dynamicValidateForm.departmentName = this.departmentArray[i].name
            return
          }
        }
      },

      changePageNo(pageNo){
        this.pagesRs.pageNo = pageNo
        this.getDatas()
      },
      changePageSize(size){
        this.pagesRs.pageSize = size
        this.getDatas()
      },
      getMajorRow(v,op){
        if (op==='审核'){
          this.handleAuditRule(v)
        }
       window.console.log(v,op)
      },
      //审核
      handleAuditRule(row){
        this.auditRule({
          ruleId:row.ruleId
        }).then(()=>{
          this.getDatas()
          this.$message.success('审核通过')
        })
      },
      //在线预览
      linkRowClick(v,row){
        window.console.log(v,row)
        window.open(API_PATH+'/rule/file/download?fileName='+row.fileName+'&filePath='+row.filePath)//+'&openStyle='+'inline'
      },

      //查询列表
      getDatas(){
        this.dsLoading = true
        this.majorCol=[
          {prop: 'ruleName', label: '规则名称'},
          {prop: 'ruleType', label: '规则分类'},
          {prop: 'department', label: '归口部门'},
          {prop: 'ywjd', label: '业务流程阶段',},
          {prop: 'ywsx', label: '业务事项',},
          {prop: 'riskLevel', label: '风险等级'},
          {prop: 'zrgw', label: '责任岗位'},
          {prop: 'ruleObj', label: '规则实例名'},
          {prop: 'wjtwsy', label: '文件条文索引'},
          {prop: 'files', label: '关联知识库',type:'link',key:'documentId'},
          {prop: 'isOpen', label: '是否启用'},
          {prop: 'statusType', label: '状态'},
          {type: 'operate', linkName: ['审核'], label: '操作', width: '120'}]
        this.queryRuleList({
          current:this.pagesRs.pageNo,
          size:this.pagesRs.pageSize,
          "keyword": this.keyWords,
          "auditStatus": '1',//0:审核不通过、1:待审核、2:审核通过、-1:已撤销、-2:已废除
        }).then((res)=>{
          this.pagesRs.total = res.records
          this.majorArray = res.rows.filter(v=>{
            if (v.isOpen===1){
              return v.isOpen = '是';
            }else {
              return   v.isOpen = '否';
            }
          })
          this.majorArray = this.majorArray.filter(v=>{
            if (v.statusType===1){
              return v.statusType = '待审核';
            }else {
              return v.statusType = '其他';
            }
          })
        }).finally(()=>{
          this.dsLoading = false
        })
      },
    },
  };
</script>

<style lang="scss" scoped>
    .myCheck {
        width: 100%;
        height: 100%;
        padding: 20px;
        box-sizing: border-box;
        .nav{
            height: 33px;
            line-height: 33px;
            margin-bottom: 6px;
            .input_c{
                width: 200px;
                margin: 0 20px 0 10px;
            }
        }
    }
</style>