<template>
  <div class="project-manage-page">
    <div class="form-container">
      <manage-form ref="projectManageForm" @serachEvent="serachEvent" @batchDelete="batchDelete">
      </manage-form>
    </div>
    <div class="table-container" :class="$root.themeHomeChange === '1' ?'table-containerL':''">
      <manage-table ref="projectManageTable" @exportTable="exportTable" @editRow="editRow">
      </manage-table>
    </div>
  </div>
</template>
<script>
import manageForm from "../component/manage/manageForm";
import manageTable from "../component/manage/manageTable";

export default {
  name: "projectManagePage",
  components: { manageTable, manageForm },
  data() {
    return {};
  },
  watch: {},
  mounted() {},
  methods: {
    //查询筛选列表
    serachEvent(data) {
      let form = {
        keyValue: data.keyValue||'',
        ruleType: data.projectName||'',
        riskRank: data.projectType||'',
        resJobs: data.resJobs||'',
        department: data.team||'',
      }
      this.$refs.projectManageTable.initForm(form)
    },

    //批量删除
    batchDelete(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$refs.projectManageTable.deleteRows()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

    //编辑
    editRow(row){
      this.$refs.projectManageForm.editDialogClick(row)
    },

    /**
     * @description:更新表格
     */
    reloadTable() {},

    /**
     * @description:导出表格
     */
    exportTable() {},
  },
};
</script>
<style lang="scss" scoped>
  .table-containerL{
    padding: 6px;
    box-sizing: border-box;
    background: #eef3ff!important;
  }
</style>