<template>
  <div class="project-manage-table" ref="projectManageTableRef" :class="$root.themeHomeChange === '1' ?'manage_table_light':''">
    <bda-scroll-table
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        v-loading="tableLoading"
      background
      :data="tableData"
        row-key="id"
      ref="table"
      popper-append-to-body
      border
      :blInitLoad="false"
      tooltip-effect="light"
      :style="{ height: tableHeight }"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        style="background-color: #041338"
        type="selection"
        :reserve-selection="true"
        width="48"
      >
      </el-table-column>
      <el-table-column
        type="index"
        label="序号"
        min-width="60"
      ></el-table-column>
      <el-table-column prop="ruleName" label="规则名称" min-width="160" show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="ruleType"
        label="规则分类"
        min-width="100"
      ></el-table-column>
      <el-table-column
        prop="department"
        label="归口部门"
        show-overflow-tooltip
        min-width="100"
      ></el-table-column>
      <el-table-column
        prop="ywjd"
        label="业务流程阶段"
        min-width="140"
      ></el-table-column>
      <el-table-column
        prop="ywsx"
        show-overflow-tooltip
        label="业务事项"
        min-width="168"
      ></el-table-column>
      <el-table-column prop="ruleDefine" label="定义规则" min-width="168" show-overflow-tooltip ></el-table-column>
      <el-table-column
        prop="riskLevel"
        label="风险等级"
        min-width="100"
      ></el-table-column>
      <el-table-column
        prop="zrgw"
        label="责任岗位"
        min-width="120"
      ></el-table-column>
      <el-table-column
        prop="ruleObj"
        show-overflow-tooltip
        label="规则实例名"
        min-width="168"
      ></el-table-column>
      <el-table-column prop="wjtwsy" label="文件条文索引" min-width="168" show-overflow-tooltip> </el-table-column>
      <el-table-column prop="glzsk" label="关联知识库" min-width="208" :show-overflow-tooltip="false">
        <template slot-scope="scope">
          <div v-for="item in scope.row.files"
               :key="item.zskId"
               style="cursor: pointer"
               @click="viewFile(item)"
               :title="item.fileName" >{{item.fileName}}</div>
        </template>
      </el-table-column>
      <el-table-column
        prop="isOpen"
        label="是否启用"
        min-width="80"
      >
        <template slot-scope="scope">
          <el-switch
              v-model="scope.row.isOpen"
              active-color="#13ce66"
              @change="HandleIsOpen(scope.row)"
              inactive-color="#ff4949">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="100">
        <template slot-scope="scope">
          <p
            @click="redactClick(scope.row)"
            style="cursor: pointer; color: #66b9ff"
          >
            编辑
          </p>
        </template>
      </el-table-column>
    </bda-scroll-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfoConfig.page"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size="pageInfoConfig.rows"
      background
      layout="prev, pager, next, sizes, jumper, total"
      :total="pageInfoConfig.total"
    >
    </el-pagination>
  </div>
</template>
<script>
import Interface from "../../../../base/mixin/enterprise-manage-center";
import {
  API_PATH
} from '@/config'
export default {
  name: "projectManageTable",
  mixins: [Interface],
  data() {
    return {
      /*表格数据 */
      tableData: [],
      tableData2: [], //存放筛选数据

      pageInfoConfig: {
        page: 1,
        rows: 10,
        total: 0,
      }, //表格-分页初始配置
      tableHeight: "",
      selectData: [], //已选数据
      formData:{
        keyValue: '',
        ruleType:'',
        riskRank: '',
        resJobs: '',
        department: '',
      },
      tableLoading:false
    };
  },
  computed: {},
  mounted() {
    this.getTableData()
  },
  methods: {
    //在线预览
    viewFile(row){
      // this.onlinePreview({
      //   filePath:row.filePath
      // }).then((res)=>{
      //   window.open(`/file-view/onlinePreview?url=${res}`);
      // })
      window.open(API_PATH+'/rule/file/download?fileName='+row.fileName+'&filePath='+row.filePath)//+'&openStyle='+'inline'
      // this.downloadGgg({
      //   fileName:row.fileName,
      //   filePath:row.filePath,
      // }).then(()=>{
      //
      // })

    },
    initForm(data){
      this.formData = data
      //查询重置分页
      this.pageInfoConfig.page = 1
      this.pageInfoConfig.rows = 10
      this.getTableData()
    },
    HandleIsOpen(v){
      this.handleIsOpen({
        isOpen:v.isOpen?1:0,
        ruleId:v.ruleId
      }).then(()=>{

      }).finally(()=>{
        this.getTableData()
      })
    },
    getTableData(){
      this.tableLoading = true
      let status = ''
      let router = this.$route.query.mark
      status = router==='myCenter'?'2':null
      this.queryRuleList({
        "departmentCode": this.formData.department,
        "keyword": this.formData.keyValue,
        "riskLevel": this.formData.riskRank,
        "ruleType": this.formData.ruleType,
        "zrgw": this.formData.resJobs,
        "auditStatus": status,//0:审核不通过、1:待审核、2:审核通过、-1:已撤销、-2:已废除
        "current":this.pageInfoConfig.page,
        "size": this.pageInfoConfig.rows,
      }).then((res)=>{
        res.rows.filter(v=>{
          if (v.isOpen==1&&v.isOpen){
            return v.isOpen = true
          }else {
            return v.isOpen = false
          }
        })
        this.tableData = res.rows
        this.pageInfoConfig.total = res.records
      }).finally(()=>{
        this.tableLoading = false
      })

    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.table.toggleRowSelection(row);
        });
      } else {
        this.$refs.table.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selectData = val;
    },
    //
    deleteRows(){
      if (this.selectData.length<1){
        this.$message.warning('请选择需要删除的值！')
        return
      }
      let selectIds = []
      selectIds = this.selectData.map(v=>{
        return v.id
      })
      let params = new URLSearchParams()
      for (let i=0;i<selectIds.length;i++){
        params.append('ids',selectIds[i])
      }
      this.ruleDelete(params).then(()=>{
        this.pageInfoConfig.page
        this.getTableData()
      }).finally(()=>{
      })
    },
    handleSizeChange(val) {
      this.pageInfoConfig.rows = val
      this.getTableData()
      window.console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pageInfoConfig.page = val
      this.getTableData()
      window.console.log(`当前页: ${val}`);
    },

    // 编辑
    redactClick(row) {
      this.$emit('editRow',row)
    },
  },
};
</script>
<style lang="scss" scoped>
.project-manage-table {
  height: 100%;
  width: 100%;
  ::v-deep .bda-main .bda-table{
    overflow-y: scroll !important;
    .el-table{
      top: 0!important;
    }
    .el-table:before{
      width: 0;
    }
    .el-table--border:after{
      width: 0;
    }
  }
  /deep/ {
    .button-container {
      margin-bottom: 10px;
    }

    /*修改表格单元格高度*/
    .bda-pagination-table {
      .bda-pagination-table-page {
        border: 0 !important;
        height: 50px !important;
      }
      .bda-pagination-table-main {
        height: calc(100% - 40px);
      }
    }

    /*checkbox样式*/

    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }

    .el-checkbox__input.is-disabled .el-checkbox__inner,
    .el-checkbox__input .el-checkbox__inner {
      background-color: #041338 !important;
      width: 16px;
      height: 16px;
      border: 1px solid #4389f9;
      border-radius: 2px;
    }

    /*解决调整单元格高度后，fixed错位*/
    .el-table__fixed-body-wrapper {
      top: 37px !important;
    }

    /*解决表格滚动高度固定导致的问题*/
    .el-table__body-wrapper.is-scrolling-left {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-right {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-middle {
      height: calc(100% - 48px) !important;
    }
    .el-table__fixed-body-wrapper {
      height: calc(100% - 34px) !important;
    }

    /*解决调整列宽度时，出现列内容未完整显示的问题*/
    .el-table .cell.el-tooltip {
      width: 100% !important;
    }

    .el-table__header-wrapper {
      background-color: #22519c;
    }

    .el-table__body-wrapper {
      background-color: #071a47;
    }

    .el-table__header th {
      background-color: #22519c !important;
      height: 48px;
      border-color: #000000;

      .cell {
        color: #fff !important;
        text-align: center;
      }
    }

    .bda-table-main {
      background-color: #071a47;
    }

    .bda-main {
      height: calc(100% - 52px);
      border: 1px solid #000000;
      .el-table__row {
        &:hover > td {
          background-color: #284385 !important;
        }
        > td {
          height: 48px;
          border-color: #000000;

          .cell {
            color: #fff;
            text-align: center;
          }
        }
        > td:nth-last-child(3) {
          .cell {
            color: #66b9ff;
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #223a75;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #1a3066;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
      .btn-next,
      .btn-prev,
      .el-pager li,
      .el-select .el-input .el-input__inner,
      .el-pagination__editor.el-input .el-input__inner {
        background-color: #041338;
        color: #d3d3d3;
        border: 1px solid #2a5cb0;
        border-radius: 3px;
      }
      .el-pager li:not(.disabled).active {
        background-color: #4389f9;
      }
      .el-pagination__jump,
      .el-pagination__total {
        color: #fff;
        margin-left: 10px;
      }
    }
  }
}
.manage_table_light{
  /deep/ {
    .el-checkbox__inner:after{
      border-color: #333!important;
    }
    //样式整改
    .table-container {
      background-color: #ffffff!important;
    }
    //样式整改
    .button-container {
      margin-bottom: 10px;
    }

    /*修改表格单元格高度*/
    .bda-pagination-table {
      .bda-pagination-table-page {
        border: 0 !important;
        height: 50px !important;
      }
      .bda-pagination-table-main {
        height: calc(100% - 40px);
      }
    }
    .el-form-item__label {
      color: #7f7f7f;
    }
    /*checkbox样式*/

    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }

    .el-checkbox__input.is-disabled .el-checkbox__inner,
    .el-checkbox__input .el-checkbox__inner {
      background-color: #fff !important;
      width: 16px;
      height: 16px;
      border: 1px solid #bbb;
      border-radius: 2px;
    }

    /*解决调整单元格高度后，fixed错位*/
    .el-table__fixed-body-wrapper {
      top: 37px !important;
    }

    /*解决表格滚动高度固定导致的问题*/
    .el-table__body-wrapper.is-scrolling-left {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-right {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-middle {
      height: calc(100% - 48px) !important;
    }
    .el-table__fixed-body-wrapper {
      height: calc(100% - 34px) !important;
    }

    /*解决调整列宽度时，出现列内容未完整显示的问题*/
    .el-table .cell.el-tooltip {
      width: 100% !important;
    }

    .el-table__header-wrapper {
      background-color: #22519c;
    }

    .el-table__body-wrapper {
      background-color: #fff;
    }

    .el-table__header th {
      // background-color: #22519c !important;
      background: rgba(188, 228, 249, 0.8)!important;
      height: 48px;
      border-color: #bbb;

      .cell {
        color: #666 !important;
        text-align: center;
      }
    }

    .bda-table-main {
      background-color: #ffffff;
    }

    .bda-main {
      /*height: calc(100% - 52px);*/
      border: 1px solid #bbb;
      .el-table__row {
        &:hover > td {
          background-color: #ebfcff !important;
        }
        > td {
          height: 48px;
          border-color: #bbb;

          .cell {
            color: #333;
            text-align: center;
            .el-button {
              color: #66b9ff;
            }
          }
        }
        > td:nth-last-child(3) {
          .cell {
            color: #66B9FF;
            text-align: center;
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #fff;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #fff;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
       .btn-next,
       .btn-prev,
       .el-pager li,
       .el-select .el-input .el-input__inner,
       .el-pagination__editor.el-input .el-input__inner {
         background: rgba(67,137,249,0.06);
         color: #333333;
         border: 0px solid #2a5cb0;
         border-radius: 4px;
       }
       .el-pager li:not(.disabled).active {
         background-color: #4389f9;
       }
      .el-pagination__jump,
      .el-pagination__total {
         color: #333333;
        margin-left: 10px;
      }
    }
  }
}
</style>